

import Vue from 'vue'
import ElementUI from 'element-ui';
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import router from'./router/memberRouter.js'      
import Base from './assets/js/common.js'         
import stores from './store/store'                
import './assets/css/base.css'                    
import 'element-ui/lib/theme-chalk/index.css'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import DatetimePlugin from 'vux/src/plugins/datetime/index.js'

Vue.use(DatetimePlugin)
Vue.use(AlertPlugin)
Vue.use(ToastPlugin, {position: 'top'})
FastClick.attach(document.body)
Vue.use(ElementUI);
Vue.config.productionTip = false

Vue.prototype.common= Base.common;  
Vue.use(VueRouter)

var globalVm=new Vue({
router,            //-----router文件
el: '#app',
store:stores,          //-----全局变量
template: '<App/>',
components: { App }
})



