
import Selector from 'vux/src/components/selector/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { setTimeout } from 'timers';
import api from '@/assets/js/api.js'
export default {
data(){
return {
formKey:'',//表单key
formId:'',//表单id
flowname:'',//流程名称
flowId:'',//流程Id
inputValue:[],//input输入框的值
selectedList:[],//复选框选中的值
cellText:'点我选择',
dialogShow:true,
showarrow:'up',
propsTree: {
label: 'name',
children: 'zones',
isLeaf: 'leaf'
},
dialogmaxwidth:'400px',
show:false,
buttonHeight:'32px',
title:'单选',
options:['aa','bb'],
value:'',
valueckeck:[],
checklist:'',
resObject:'',
props: { multiple: true },
Vvalue:'',
value: 1,
value7:'',
partoptions:[{key:'one',value:'radio1'}],
value6:[],
option:[
{key:'name',value:'篮球'},
{key:'name1',value:'足球'},
{key:'name2',value:'游泳'}
],
value5:[],
value4:'开始时间',
value3:'',
value2:['华为'],
dataList:[{name:'小米',id:1}, {name:'iPhone',id:2}]
}
},methods:{
cellclick(){
console.log("点击cell");
this.show=true;
},
whichOne(value,label){
console.log("value=="+value+"label=="+label);
},
async submit(){//提交流程表单
let _this = this;
console.log(_this.inputValue.length);
if(_this.cellText==='点我选择' && _this.inputValue.length<1){//如果表单信息未选中，直接跳出
_this.common.showAlert('温馨提示','请填写表单信息');
return;
}
let inputKey = [];
for(let i=0;i<_this.inputValue.length;i++){
inputKey.push(_this.resObject[i].id);
}

var params={
processDefinitionId: _this.flowId,
name: _this.flowname,
formId:_this.formId,
key:_this.formKey,
inputKey:inputKey,
inputValue:_this.inputValue
};
let result = await api.submitFlowForm(params).then(res=>{
var responseCode = res.status;
if(responseCode === 200){
_this.common.showAlert('温馨提示','流程提交成功');
setTimeout(function(){
_this.$router.push({
path: '/memberHome/flowlaunch',
});
},500)
}else{
_this.common.showAlert('系统异常','提交流程失败');
}
});
},
async loadFlowForms(flowId){
let _this = this;
let params = {};
params.flowId = flowId;
let result = await api.getUpFlowForm(params).then(res=>{
if(res.data!=null && res.data.fields.length > 0 ){
_this.resObject = res.data.fields;
_this.formId = res.data.id;
_this.formKey = res.data.key;
}
});
},
loadNode(node, resolve) {
if (node.level === 0) {
return resolve([{ name: "广州市玄武无线科技股份有限公司",id:"1132c6d8-3eeb-11e8-961c-005056aed64e",parentId:"0",parent_ids:"0"}]);
}else{
this.getTreeChild(node, resolve)
}
},
async getTreeChild(node,resolve){//加载组织架构树
var parent_id = node.data.id;
let  data1 = [];
let params = {};
params.parent_id = parent_id;
let result = await api.getDepartmentInfo(params).then(res=>{
this.data1 = res.data;
resolve(this.data1);
});
},
checkedComfirm(){//提交选中的复选框
console.log(this.$refs.tree.getCheckedNodes());
this.selectedList = this.$refs.tree.getCheckedNodes();
if(this.selectedList.length===0){
this.cellText ='点我选择';
this.show = false;
return;
}
let str = '';
for(let i=0;i<this.selectedList.length;i++){
str+= this.selectedList[i].name+",";
}
this.cellText = str.substring(0,str.length-1);
console.log(this.cellText);
this.show = false;
},
resetChecked(){//清除选中的复选框
this.$refs.tree.setCheckedKeys([]);
},
inputChange(index){
console.log(index);
}
},components:{
Selector,
XSwitch ,
XDialog ,
XButton,
Radio,
CellBox,
Cell,
Group,
Calendar,
Datetime,
XTextarea,
PopupRadio,
PopupPicker,
Picker,
Checklist,
XInput,
Checker,
CheckerItem
},mounted(){
this.flowname = this.$route.query.flowname;
this.flowId = this.$route.query.flowId;
this.loadFlowForms(this.flowId);
this.$store.commit('UPDATE_PAGE_TITLE',this.flowname);
},watch:{

}
}



